import { useState } from "react";
import "./styles.css";

function App() {
  const [input1, setInput1] = useState("");
  const [input2, setInput2] = useState("");
  const [relationship, setRelationShip] = useState("");
  let [arr, setArr] = useState([]);

  const handleRelationship = () => {
    // console.log('clicked');
    let str1 = input1.toLocaleLowerCase();
    let str2 = input2.toLocaleLowerCase();

    if (str1 === "" || str2 === "") {
      setRelationShip("Please Enter valid input");
      return;
    }

    // Getting both names
    let bothNames = str1 + str2;

    // Checking for repeated Character
    const nameArray = [...bothNames].sort();

    const repeatedChars = [];
    for (let i = 0; i < nameArray.length - 1; i++) {
      if (nameArray[i] === nameArray[i + 1]) {
        repeatedChars.push(nameArray[i]);
      }
    }

    // Getting repeated char array
    const repeatedCharArray = [...new Set(repeatedChars)];
    // console.log(repeatedCharArray);

    // Remove all character of both names
    for (let i = 0; i < bothNames.length; i++) {
      arr.push(bothNames.charAt(i));
      //now remove repeated char
      for (let j = 0; j < repeatedCharArray.length; j++) {
        arr = arr.filter((item) => item !== repeatedCharArray[j]);
      }
    }

    const bothNameFinalLength = arr.length;

    if (bothNameFinalLength % 6 === 1) {
      setRelationShip("Friends");
    } else if (bothNameFinalLength % 6 === 2) {
      setRelationShip("Love");
    } else if (bothNameFinalLength % 6 === 3) {
      setRelationShip("Affection");
    } else if (bothNameFinalLength % 6 === 4) {
      setRelationShip("Marriage");
    } else if (bothNameFinalLength % 6 === 5) {
      setRelationShip("Enemy");
    } else if (bothNameFinalLength % 6 === 0) {
      setRelationShip("Siblings");
    }

    // console.log(arr, bothNameFinalLength);
  };

  const handleClear = () => {
    setInput1("");
    setInput2("");
    setRelationShip("Please Enter valid input");
  };

  return (
    <div className="App">
      <label htmlFor="input1">First Name: </label>
      <input
        type="text"
        data-testid="input1"
        value={input1}
        onChange={(e) => setInput1(e.target.value)}
      />{" "}
      <br />
      <br />
      <label htmlFor="input2">Second Name: </label>
      <input
        type="text"
        data-testid="input2"
        value={input2}
        onChange={(e) => setInput2(e.target.value)}
      />{" "}
      <br />
      <br />
      <button data-testid="calculate_relationship" onClick={handleRelationship}>
        Calculate Relationship Future
      </button>
      <button data-testid="clear" onClick={handleClear}>
        Clear
      </button>{" "}
      <br />
      <br />
      <h3 data-testid="answer">{relationship}</h3>
    </div>
  );
}

export default App;
